#project {
    height: auto;
}

.project-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
}

.project-items {
    background: linear-gradient(45deg,rgba(0, 0, 0, .79), rgba(255,255,255,.15), rgba(0,0,0,.98));
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--trst);
}

.project-items:hover {
    border-color: #f3f3f3;
    background: transparent;
}

.project-item-img {
    border-radius: 1.5rem;
    overflow: hidden;
}

.project-item-img img:hover {
    transition: var(--trst);
    transform: scale(1.2);
}

.project-items h3 {
    text-align: center;
    margin: 1rem 0 2rem;
}

.project-item-cta {
    display: flex;
    justify-content: center;
    gap: .65rem;
    margin-bottom: .8rem;   
}

.project-item-cta svg {
    font-size: 1.5rem;
}

.project-item-cta .btn {
    letter-spacing: 2px;
}

/* media query */
@media (max-width: 1024px) {
    .project-container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
}

@media (max-width: 614px) {
    .container.project-container {
        width: 83%;
    }
    #project h2 {
        text-decoration: underline;
    }
    .project-container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .project-items {
        padding: 1.6rem;
    }
}