header {
    height: 100vh;
    padding-top: 6rem;
    overflow: hidden;
    /* background: rgba(0,0,0,.05); */
}

.header-container {
    text-align: center;
    height: 100%;
    position: relative;
    /* box-shadow: var(--mainWhite); */
}

/* contactbtn */

.contact-btn {
    margin-top: 2.6rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/* Social */

.header-social {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .75rem;
    position: absolute;
    right: 0;
    bottom: 3rem;
    /* box-shadow: var(--mainWhite); */
}

.header-social::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-light);
}

/* info */

.info {
    background: rgba(0, 0, 0, 0.35);
    width: 22rem;
    height: 14rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 40rem 20rem 20rem 40rem;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.info::after {
    content:"✨";
    font-size: 3rem;
    position: absolute;
    top: 0;
    right: 0;
}

.info img {
    border-radius: 20rem;
}

/* scroll */

.scroll-down {
    position: absolute;
    left: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: .9rem;
}

/* media query */
@media (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media (max-width: 614px) {
    header {
        height: 90vh;
    }
    header h1 {
        font-size: 2rem;
    }
    .header-social, .scroll-down {
        display: none;
    }
}